<template>
    <div>
        <el-row>
            <el-button @click="aa" type="success">返回主页</el-button>
            <el-col></el-col>
        </el-row>
        <h3>课程长这样</h3>
    </div>
</template>
<script>
export default {
    methods:{
        aa(){
            let routeData = this.$router.resolve({
                name: 'Kecheng',
            })
            window.open(routeData.href,'_blank')
        }
    }
}
</script>


