<template>
<div class="main">
    <el-container>
        <el-header class="xuexi_header">
            <el-row>
                <!-- <el-col :span="6"><br></el-col> -->
                <el-col :span="20">
                    <el-input placeholder="请输入查询内容" v-model="searchInfo" style="width:500px">
                    <i slot="suffix" class="el-input__icon el-icon-search"  @click.prevent="search"></i>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="successs">投稿</el-button>
                </el-col>
            </el-row>
            <el-row>
                <el-radio-group v-model="radio1">
                    <el-radio-button label="全部"></el-radio-button>
                    <el-radio-button label="关于擎天"></el-radio-button>
                    <el-radio-button label="产品学习"></el-radio-button>
                    <el-radio-button label="技术学习"></el-radio-button>
                    <el-radio-button label="职业发展"></el-radio-button>
                </el-radio-group>
            </el-row>
            <el-row  v-show="list_show">
                <el-radio-group v-model="radio2">
                    <el-radio-button label="全部"></el-radio-button>
                    <el-radio-button v-for="(item,index) in list" :index="item" :key="index" :label="item"></el-radio-button>
                </el-radio-group>
            </el-row>
            <el-row>
                <el-radio-group v-model="radio3">
                    <el-radio-button label="全部" active></el-radio-button>
                    <el-radio-button label="视频"></el-radio-button>
                    <el-radio-button label="图片"></el-radio-button>
                    <el-radio-button label="文档"></el-radio-button>
                </el-radio-group>
            </el-row>
            <el-row>
                <el-radio-group v-model="radio4">
                    <el-radio-button label="全部" active></el-radio-button>
                    <el-radio-button label="按发布时间排序"></el-radio-button>
                    <el-radio-button label="按播放量"></el-radio-button>
                </el-radio-group>
            </el-row>
            <!-- <el-row class="main3">
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('0')">全部</span>
                </el-col>
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('1')">视频</span>
                </el-col>
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('2')">文档</span>
                </el-col>
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('3')">音频</span>
                </el-col>
            </el-row>
            <el-row class="main3">
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('0')">全部</span>
                </el-col>
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('1')">按时间排序</span>
                </el-col>
                <el-col :span="2">
                    <span role="button" tabindex="0" @click="test2('2')">按播放排序</span>
                </el-col>
            </el-row> -->
            
        </el-header>
        <!-- 主体内容 -->
        <el-main>
                <h3>主页内容==========================</h3><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
        </el-main>
    </el-container> 
</div>
</template>
<script>
export default {
  
    data(){
        return{
            radio1:'',
            radio2:'',
            radio3:'',
            radio4:'',
            searchInfo:'',
            // 办公软件学习/职场实用技能/演讲与主持/人际交流
            list:[],
            list_show:false,
            list1:['公司简介','企业文化','发展历程','合作伙伴'],
            list2:['产品设计','产品运营','数据分析','用户研究','原型设计'],
            list3:['前沿技术','程序设计与开发','计算机基础与应用软件工程','网络与安全技术','硬软件系统及原理'],
            list4:['办公软件学习','职场实用技能','演讲与主持','人际交流'],
        }
    },
    watch:{
        radio1() {
            if(this.radio1=='全部'){
                  this.list_show=false
            }
            else{
                this.list_show=true
            }
            switch(this.radio1){
                case '全部':this.list=[];break;
                case '关于擎天':this.list=this.list1;break;
                case '产品学习':this.list=this.list2;break;
                case'技术学习':this.list=this.list3;break;
                case'职业发展':this.list=this.list4;break;
            }   
        }
    },
    methods:{
        test(val){
            this.type1=val
            alert(":"+this.type1)
        },
        test2(val){
            this.type2=val
            alert(this.type2)
        }
    }
}
</script>
<style scoped>
.main{
    width: 100%;

}
.el-main{
    margin-top:30%
}
.xuexi_header{
    /* background-color: white; */
}
</style>


