import Vue from 'vue'
import Router from 'vue-router'

// 登录、注册、主页
import Home from '@/components/home/index'
import Login from '@/components/login/index'
import Register from '@/components/register/index.vue'

// 5个首页导航
import Person from '@/components/home/person.vue'
import Control from '@/components/home/control.vue'
import Xuexi from '@/components/home/xuexi.vue'
import Xuexi2 from '@/components/home/xuexi2.vue'
// import Music from '@/components/home/music.vue'
import Shouye from '@/components/home/shouye.vue'
import Luntan from '@/components/home/luntan.vue'

// 个人中心
import Person_a1 from '@/components/home/person/person_a1.vue'
import Person_a2 from '@/components/home/person/person_a2.vue'
import Person_b1 from '@/components/home/person/person_b1.vue'
import Person_c1 from '@/components/home/person/person_c1.vue'
import Person_c2 from '@/components/home/person/person_c2.vue'
import Person_c3 from '@/components/home/person/person_c3.vue'
import Person_c4 from '@/components/home/person/person_c4.vue'
import Person_d1 from '@/components/home/person/person_d1.vue'

// 管理中心

// 课程学习ing
import Kecheng from '@/components/home/kecheng.vue'
// 资料学习ing
import Ziliao from '@/components/home/ziliao.vue'
Vue.use(Router)

export default new Router({
  // mode:History,
  routes: [
    {
      path: '/',
      name: '',
      redirect:'/login'
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/kecheng',
      name :'Kecheng',
      component: Kecheng,
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      // children: [
      //   {
      //     path: '/test',
      //     name: 'Test',
      //     component: Test,
      //   }
      // ]
    },
    {
      path: '/home',
      name :'Home',
      component: Home,
      children: [
        {
          path: '/person',
          name :'Person',
          component: Person,
          children: [
            {
              path: '/person_a1',
              name :'Person_a1',
              component: Person_a1,
            },
            {
              path: '/person_a2',
              name :'Person_a2',
              component: Person_a2,
            },
            {
              path: '/person_b1',
              name :'Person_b1',
              component: Person_b1,
            },
            {
              path: '/person_c1',
              name :'Person_c1',
              component: Person_c1,
            },
            {
              path: '/person_c2',
              name :'Person_c2',
              component: Person_c2,
            },
            {
              path: '/person_c3',
              name :'Person_c3',
              component: Person_c3,
            },
            {
              path: '/person_c4',
              name :'Person_c4',
              component: Person_c4,
            },
            {
              path: '/person_d1',
              name :'Person_d1',
              component: Person_d1,
            },
          ]
        },
        {
          path: '/control',
          name :'Control',
          component: Control,
        },
        {
          path: '/xuexi',
          name :'Xuexi',
          component: Xuexi,
        },
        {
          path: '/xuexi2',
          name :'Xuexi2',
          component: Xuexi2
        },{
          path: '/shouye',
          name :'Shouye',
          component: Shouye,
        },
        {
          path: '/luntan',
          name :'Luntan',
          component: Luntan,
        },
        
        {
          path: '/ziliao',
          name :'Ziliao',
          component: Ziliao,
        },
      ]
    }
  ]
})



